/**
 *  :
 * /\b[A-G][b#]?(maj|min|m|M|\+|-|dim|aug)?\d*(sus)?\d*(\/[A-G][b#]?)?\b/g
 *   'M' (       'FM')      Cadd9
 * TODO:        to be ("Am I")?
 */
export const CHORD_REGEX_PATTERN =
    /\b[A-H][b#]?(maj|min|m|\+|-|dim|aug|add)?\d*(sus)?\d*(\/[A-G][b#]?)?\b/g;

export interface IChord {
    chord: string;
    name: string;
    src: string;
}

export type IChordsList = Record<string, IChord[]>;

